<template>
	<div class="index" v-if="person">
		<div class="header">
			<div class="broadcast">
				<img src="@/assets/images/broad.png" alt="">
				绑定后可使用医保个人账户余额购买保险
			</div>
			<div class="message-box">
				<div class="message clearfix">
					<div class="left">
						<div class="abs-top">
							医保个人账户余额<br /><br />
							<span class="price">{{person.medicalBalance}}</span><span class="yan">元</span>
						</div>
						<div class="abs-bottom">
							<table>
								<thead>
									<tr>
										<th>参保类型</th>
										<th>参保地</th>
									</tr>
								</thead>
								<tbody>
									<tr>
										<td>{{person.medical_type}}</td>
										<td>{{person.insured_place}}</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
					<div class="right">
						<div class="user">
							<img src="@/assets/images/avatar.png">
							<span class="name" v-if="person.medical_insurance_card_num">{{person.name}}</span>
							<span class="bind" v-else>绑定医保</span>
						</div>
						<div class="toGavermen">查看福州个账医保政策 &gt;</div>
					</div>
				</div>
			</div>
		</div>
		<div class="tipBox">以下为您推荐福州地区医保用户适用的保险产品</div>
		<product :productInfo='product' v-if="product"></product>
	</div>
</template>

<script>
	
import {mapMutations,mapGetters} from 'vuex';
import product from '@/components/product/BaseProduct.vue';
export default{
	data(){
		return {
			person: null,
			product: null
		}
	},
	methods:{
		...mapMutations(['SET_USER_INFO','SET_TOKEN'])
	},
	created(){
		this.$api.home.getPersonMessage({
			token: this.$route.params.token
		}).then(res=>{
			if(res.code){
				this.person = JSON.parse(JSON.stringify(res.data))
				this.SET_USER_INFO(res.data)
				this.SET_TOKEN(this.$route.params.token);
			}
		})
		this.$api.product.productDetail({
			id: 11
		}).then(res=>{
			console.log('res',res);
			this.product = res.data.product
		})
	},
	components: {
		product
	}
}
</script>

<style lang="scss" scoped>
